import React, { Component } from 'react';
import './App.css';

import MuiThemeProvider from 'material-ui/styles/MuiThemeProvider';
import getMuiTheme from 'material-ui/styles/getMuiTheme';
import AppBar from 'material-ui/AppBar';
import Drawer from 'material-ui/Drawer';
import MenuItem from 'material-ui/MenuItem';
import Dialog from 'material-ui/Dialog';
import TextField from 'material-ui/TextField';
import RaisedButton from 'material-ui/RaisedButton';
import DatePicker from 'material-ui/DatePicker';
import FlatButton from 'material-ui/FlatButton';
import FontIcon from 'material-ui/FontIcon';
import RefreshIndicator from 'material-ui/RefreshIndicator';
import {Card, CardActions, CardHeader, CardMedia, CardTitle, CardText} from 'material-ui/Card';
import Badge from 'material-ui/Badge';
import Snackbar from 'material-ui/Snackbar';

import { Switch, Route, withRouter } from 'react-router-dom';
import NumberFormat from 'react-number-format';

class OverleyLoading  extends Component {
  render(){
    return (
      <div>
        <div style={{
          backgroundColor: '#000000',
          opacity: .4,
          position: 'absolute',
          zIndex: 2000,
          top: 0,
          left: 0,
          bottom: 0,
          right: 0
        }}>

        </div>
        <RefreshIndicator
          size={40}
          left={-20}
          top={10}
          status={'loading'}
          style={{zIndex: 3000,marginLeft: '50%', marginTop: '50%'}}
        />
      </div>
    );
  }
}
class Login extends Component {
  state = {
    author: '',
    text: '',
    openDialog: false,
    documento: '',
    senha: '',
    loading: false,
  }

  handleClick(){
    this.setState({ loading: true });
    setTimeout(()=>{
      this.setState({loading: false});
      this.props.history.push("/map");
    }, 3000);

  }

  openDialog(bool){
    this.setState({ openDialog: bool });
  }

  handleLogin(){
    this.handleClick();
  }

  render(){
    const actionsButtons = [
      <FlatButton label="OK" onClick={ ()=>{ this.openDialog(false) }}/>
    ];
    //"###.###.###-##"
    //"##.###.###/####-##"
    return (
      <div style={{backgroundColor: '#363E46'}}>
        {this.state.loading && <OverleyLoading/>}
        <Dialog
          title="Recuperar senha"
          actions={actionsButtons}
          modal={true}
          open={this.state.openDialog}
          onRequestClose={this.handleClose}>
          Por favor! Verifique sua caixa de entrada do e-mail!
        </Dialog>
        <br/>
        <img src='logo_concept.png' alt="Concept" style={{width:'250px', height:'auto', margin: 'auto', padding: '20px'}}/><br/><br/>

        <div style={{backgroundColor: 'white', height: '180px', width: '250px', margin: 'auto'}}>
        <NumberFormat
          onKeyUp={(e) => { if(e.keyCode === 13){ this.refs.senha.focus();} } }
          floatingLabelText="CPF/CNPJ"
          customInput={TextField}
          format={0 < 13 ? "##.###.###/####-##" : "###.###.###-##"}
          type="tel"
          style={{width: '200px'}}
          onChange = {(event,newValue) => {console.log(event.target.value);this.setState({documento: event.target.value}); }}
          />
         <br/>
        <TextField
          onKeyUp={(e) => {
            if(e.keyCode === 13){
              this.refs.senha.blur();
              this.handleLogin();
            }
          } }
          ref="senha"
          type="password"
          floatingLabelText="Senha"
          style={{width: '200px' }}
          onChange = {(event,newValue) => this.setState({password:newValue})} />
        </div>

         <br/>
         <br/>
         <br/>
         <RaisedButton secondary={true} label="Entrar" onClick={(event) => this.handleClick(event)} style={{width: '250px'}}/>
         <br/>
         <br/>
         <FlatButton label="Esqueci a senha" style={{width: '250px', color: 'white'}} onClick={ ()=>{ this.openDialog(true) }}/>
      </div>
    );
  }
}

class Warnings extends Component {
  render(){
    return (
      <div style={{height: '100vh'}} />
    );
  }
}


const styles = {
  root: {
    display: 'flex',
    flexWrap: 'wrap',
    justifyContent: 'space-around',
  },
  gridList: {
    width: 300,
    height: 200,
    overflowY: 'auto',
  },
};

class Map extends Component {
  state = {
    openDialogBlock: false,
    showSnake: false,
  }

  openDialogBlock(value){
    this.setState({ openDialogBlock: value });
  }

  handleRequestCloseSnake = () => {
    this.setState({
      showSnake: false,
    });
  };

  render(){
    const actionsButtons = [
      <FlatButton label="Cancelar" onClick={ ()=>{ this.openDialogBlock(false); }}/>,
      <FlatButton label="Enviar" onClick={ ()=>{ this.openDialogBlock(false); this.setState({showSnake: true})}}/>

    ];

    return (
      <div style={{height: '100vh'}}>
        <Snackbar
          open={this.state.showSnake}
          message="Veiculo NNB-8204 foi bloquado"
          autoHideDuration={3000}
          onRequestClose={this.handleRequestCloseSnake}
        />

        <Dialog
          ref="senha"
          title="Envio de bloqueo"
          actions={actionsButtons}
          open={this.state.openDialogBlock}
          onRequestClose={this.handleClose}>
            <TextField
              onKeyUp={(e) => {
                if(e.keyCode === 13){
                  this.handleLogin();
                }
              } }
              floatingLabelText="Senha para bloquear"
              style={{width: '100%' }}
              onChange = {(event,newValue) => this.setState({password:newValue})} />

        </Dialog>


        <Card style={{margin: 5}}>
          <CardHeader
            title="NNB 8204"
            subtitle="Toyota Corola"
          >
            <FontIcon className="material-icons" >lock_open</FontIcon>
            <FontIcon className="material-icons" >signal_cellular_off</FontIcon>
            <FontIcon className="material-icons" color='black'>directions_car</FontIcon>
            <FlatButton onClick={()=>{ this.openDialogBlock(true) }}>
              <FontIcon className="material-icons" color='#cc0000'>lock</FontIcon>
            </FlatButton>
          </CardHeader>
        </Card>

        <Card style={{margin: 5}}>
          <CardHeader
            title="PIE 5277"
            subtitle="Instaladores"
          >
            <FontIcon className="material-icons" >lock_open</FontIcon>
            <FontIcon className="material-icons" >signal_cellular_4_bar</FontIcon>
            <FontIcon className="material-icons" color='black'>motorcycle</FontIcon>
            <FlatButton onClick={()=>{ this.openDialogBlock(true) }}>
              <FontIcon className="material-icons" color='#cc0000'>lock</FontIcon>
            </FlatButton>
          </CardHeader>
        </Card>
      </div>
    );
  }
}

let DateTimeFormat = global.Intl.DateTimeFormat;

class Routes extends Component {
  render(){
    return (
      <div style={{height: '100vh', paddingTop: '50px'}}>
        <h5>Dia do percurso</h5>
        <DatePicker
          hintText="Dia do percurso"
          DateTimeFormat={DateTimeFormat}
          okLabel="OK"
          cancelLabel="Cancelar"
          locale="pt"
        />
      </div>
    );
  }
}

class OffLine extends Component {
  render(){
    return (
      <div style={{height: '100vh', paddingTop: '50px'}}>
        <h5>Dia do percurso</h5>
        <DatePicker hintText="Dia do percurso" />
      </div>
    );
  }
}

class App extends Component {
  constructor(props) {
    super(props);
    this.state = {open: false};
  }

  handleClose(path){
    this.goTo(path);
    this.toggleDrawer();
  }

  toggleDrawer() {
    this.setState({
        open: !this.state.open
    })
  }

  goTo(path) {
    this.props.history.push(path);
  }

  render() {
    const muiTheme = getMuiTheme({
        palette: {
            primary1Color: '#F12E53',
            primary2Color: '#F12E53',
            accent1Color: '#F12E53',
            pickerHeaderColor: '#363E46',
        },
    });

    return (
      <MuiThemeProvider muiTheme={muiTheme}>
        <div>
          {
            this.props.location.pathname !== "/" &&
            <Drawer
              disabled={true}
              open={this.state.open}
              docked={false}
              onRequestChange={(open) => this.setState({open})}>
              <img src='logo.png' alt="Concept" style={{width:'80%', height:'auto', margin: 'auto', padding: '20px'}}/>
              <MenuItem
                leftIcon={<FontIcon className="material-icons" color={this.props.location.pathname === "/map" ? '#F12E53' : 'black' }>directions_car</FontIcon> }
                style={{ backgroundColor: this.props.location.pathname === "/map" ? '#f2f2f2' : 'white'}}
                onClick={()=> this.handleClose('/map') }>Veículo</MenuItem>
              <MenuItem
                leftIcon={<FontIcon className="material-icons" color={this.props.location.pathname === "/routes" ? '#F12E53' : 'black' }>map</FontIcon> }
                style={{ backgroundColor: this.props.location.pathname === "/routes" ? '#f2f2f2' : 'white'}}
                onClick={()=> this.handleClose('/routes') }>Percurso</MenuItem>
              <MenuItem
                leftIcon={<FontIcon className="material-icons" color={this.props.location.pathname === "/warnings" ? '#F12E53' : 'black' }>warnings</FontIcon> }
                style={{ backgroundColor: this.props.location.pathname === "/warnings" ? '#f2f2f2' : 'white'}}
                onClick={()=> this.handleClose('/warnings') }>Avisos</MenuItem>
              <MenuItem
                leftIcon={<FontIcon className="material-icons" color='black'>exit_to_app</FontIcon> }
                onClick={()=> this.handleClose('/index.html') }>Sair</MenuItem>
            </Drawer>
          }
          <div className="App">
          {
            this.props.location.pathname !== "/index.html" &&
            <AppBar
              className='app-bar'
              title='Meus veículos - 2'
              iconClassNameRight="muidocs-icon-navigation-expand-more"
              onLeftIconButtonTouchTap={this.toggleDrawer.bind(this)}
              iconElementRight={<Badge badgeContent={2} secondary={true} ><FontIcon className="material-icons" style={{top: -12, right: -12}} color='white'>notifications</FontIcon> </Badge>}
              onRightIconButtonTouchTap={()=> alert(1)}
              style={{ lineHeight: '12px', backgroundColor: '#363E46'}} />
          }
                <Switch>
                  <Route path='/index.html' component={Login}/>
                  <Route path='/routes' component={Routes}/>
                  <Route path='/map' component={Map}/>
                  <Route path='/warnings' component={Warnings}/>

                </Switch>
          </div>
        </div>
      </MuiThemeProvider>
    );
  }
}

export default withRouter(App);



